

.layout-modal-overlay {
	background: transparent;
	background: rgba(0,0,0,.5);
	opacity: 0;
	position: fixed;
	top: -110%;
	@include transition((property: opacity));
	width: 100%;
	z-index: 1000;

	.contact-modal-active & {
		height: 100%;
		top: 0;
		opacity: 1;
	}
	
	.country-modal-active & {
    height: 100%;
    top: 0;
    opacity:1;
  }
}

.layout-contact-modal {
	@include box-sizing();
	background: #fff;
	border: solid 1px #ededed;
	-webkit-box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.15);
	-moz-box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.15);
	box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.15);
	opacity: 0;
	max-height: 80%;
	max-height: calc(100% - 2em);
	overflow: auto;
	padding: em(20);
	position: fixed;
	right: -999em;
	@include transition((property: opacity));
	transition-delay: .25s;
	top: 1em;
	z-index: 1001;


	ul {
		list-style: none;
		padding: 0;

		a:after {
			content: '\203A';
			font-weight: normal;
			padding-left: em(8);
		}
	}

	a {
		font-weight: 700;
	}

	.contact-modal-active & {
		opacity: 1;
		top: 50%;
		left: 50%;
		max-width: 90%;

		// vertical center
		-webkit-transform: translate(-50%, -50%);
		    -ms-transform: translate(-50%, -50%);
		        transform: translate(-50%, -50%);

		@include media(breakpointLarge) {
			max-height: none;
			width: columns(4);
		}

		/*.oldie & {
			top: 20%;
			left: columns(4);
		}*/

	}

	.contact-modal-header {

		clear: both;

		&:after {
			clear: both;
			content: '.';
			display: table;
			visibility: hidden;
		}

		h2 {
			float: left;
			@include font-size(22.5);
			padding-right: em(24);

			@include media(breakpointLarge) {
				@include font-size(32);
			}
		}

		// Added .modal-title class to replace h2 from contact modal popup. (BMC-481)
		.modal-title{
		   float:left;
		   @include font-size(22.5);
		   padding-right: em(24);
		   color:#FE5000;
       font-weight:800;
       line-height: 1.1em;

		   @include media(breakpointLarge) {
          @include font-size(32);
       }
		}

	}

	.modal-close {

		position: absolute;
		right: em(8);
		top: em(20);

		&:before {
			@include font-size(30);
			@include icon();
			content: '\e629';
		}
	}

	.contact-modal-inner-wrap {

	}

}

//Country Modal Popup CSS 

.layout-country-modal {
  @include box-sizing();
  background: #fff;
  border: solid 1px #ededed;
  -webkit-box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.15);
  -moz-box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.15);
  box-shadow: 0px 5px 10px 3px rgba(0, 0, 0, 0.15);
  opacity: 0;
  max-height: 80%;
  max-height: calc(100% - 2em);
  overflow: auto;
  padding: em(20);
  position: fixed;
  right: -999em;
  @include transition((property: opacity));
  transition-delay: .25s;
  top: 1em;
  z-index: 1001;


  ul {
    list-style: none;
    padding: 0;
    margin-left: 0px;
    margin-bottom: 0.1em;
     li{
        margin-bottom: 0.2rem;
        font-size: 0.8rem;
      }
  }
  
  h3{
    font-size: 1rem;
  }
  
  .country-modal-active & {
    opacity: 1;
    top: 50%;
    left: 50%;
    max-width: 90%;

    // vertical center
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    @include media(breakpointLarge) {
      // max-height: none;
      width: 90%; //columns(4);
    }

    /*.oldie & {
      top: 20%;
      left: columns(4);
    }*/

  }

  .country-modal-header {

    clear: both;

    &:after {
      clear: both;
      content: '.';
      display: table;
      visibility: hidden;
      
    }

    h2 {
      float: left;
      @include font-size(22.5);
      padding-right: em(24);

      @include media(breakpointLarge) {
        @include font-size(32);
      }
    }
    
    // Added .modal-title class to replace h2 from contact modal popup.
    .modal-title{
       float:left; 
       @include font-size(22.5);
       padding-right: em(24);
       color:#FE5000; 
       font-weight:800;
       line-height: 1.1em;
       
       @include media(breakpointLarge) {
          @include font-size(32);
       }
    }

  }

  .country-modal-close {

    position: absolute;
    right: em(8);
    top: em(20);

    &:before {
      @include font-size(30);
      @include icon();
      content: '\e629';
    }
  }
  
  .country-modal-inner-wrap {

  }

}

//Country Redirect Prompt
.layout-rediect-alert 
{
  display:none;
  color: white;
  .alert {
    margin: 1.1em 0% 0%;
    width: 100%;
  }
  
  .alert-region-close::before {
      content: "";
      font-family: "icomoon";
      font-size: 1.66667rem;
      font-style: normal;
      font-variant: normal;
      font-weight: normal;
      line-height: 1;
      text-transform: none;
      position: absolute;
      right: 0.44444em;
      font-weight: 700;
      color: #29a5d6;
      text-decoration: none;
      text-shadow: 1px 1px 1px rgba(241, 93, 34, 0.07);
      transition: color 0.25s ease-out 0s;
  }
  
  p{
    margin-bottom: 10px;
  }
  
  input{
    margin-bottom: 1em;
    margin-right:10px;
  }
  
  a{
    text-align:left;
  }

} 


